import { showError } from '@nextcloud/dialogs';
import { translate as t } from '@nextcloud/l10n';
import { defineComponent } from 'vue';
import { useAuthTokenStore } from "../store/authtoken";
import NcButton from '@nextcloud/vue/components/NcButton';
import NcTextField from '@nextcloud/vue/components/NcTextField';
import AuthTokenSetupDialog from "./AuthTokenSetupDialog.vue";
import logger from "../logger";
export default defineComponent({
  name: 'AuthTokenSetup',
  components: {
    NcButton,
    NcTextField,
    AuthTokenSetupDialog
  },
  setup() {
    const authTokenStore = useAuthTokenStore();
    return {
      authTokenStore
    };
  },
  data() {
    return {
      deviceName: '',
      loading: false,
      newToken: null
    };
  },
  methods: {
    t,
    reset() {
      this.loading = false;
      this.deviceName = '';
      this.newToken = null;
    },
    async submit() {
      try {
        this.loading = true;
        this.newToken = await this.authTokenStore.addToken(this.deviceName);
      } catch (error) {
        logger.error(error);
        showError(t('settings', 'Error while creating device token'));
        this.reset();
      } finally {
        this.loading = false;
      }
    }
  }
});